<template>
    <div>
        <el-input placeholder="请输入搜索内容" v-model="search_box" class="search-box" style="width: 50ch;left:121px;padding-top: 10px">
            <el-button slot="append" icon="el-icon-search" @click="gotosearch"></el-button>
        </el-input>
        <el-menu :default-active="search_index" class="search-menu" mode="horizontal" style="padding-left: 100px；padding-left: 100px">
            <el-menu-item index="-1"  @click="change_index('-1')">综合</el-menu-item>
            <el-menu-item index="1"  @click="change_index('1')">文学院</el-menu-item>
            <el-menu-item index="2"  @click="change_index('2')">软件之家</el-menu-item>
            <el-menu-item index="3"  @click="change_index('3')">数学院</el-menu-item>
            <el-menu-item index="4"  @click="change_index('4')">法学院</el-menu-item>
            <el-menu-item index="5"  @click="change_index('5')">物理学院</el-menu-item>
        </el-menu>
        <div id="result_list">
            <div v-for="item in search_items" :key="item.url" >
                <div class="container">
                    <div class="title">
                        <a v-bind:href="item.url" v-html="item.title">
                            {{item.title}}
                        </a>
                    </div>
                    <div class="abstract" v-html="item.content">
                        {{item.content}}
                    </div>
                    <div class="f13">
                        <div class="showurl">
                        {{item.url}}
                        </div>
                            -
                        <a v-bind:href="'http://47.95.200.233:8080/ding/copy/'+item.site+'/'+item.id" target="_blank">
                            Ding快照
                        </a>
                    </div>
                </div>
            </div>
            <div v-if="is_result">
                没有找到结果
            </div>
        </div>
        <el-pagination
            style="padding-left: 111px;padding-bottom: 10ch;"
            @current-change="gotosearch"
            :current-page.sync="page_num"
            :page-size="10"
            class="page"
            layout="total, prev, pager, next"
            :total="res_total">
        </el-pagination>
    </div>
</template>
<script>
export default {
    data(){
        return {
            res_total:11,
            is_result:false,
            search_box:"",
            search_index:"-1",
            page_num: 1,
            search_items:[]
        }
    },
    mounted(){
        console.log(this.$route.params.type)
        this.search_index = this.$route.params.type
        this.search_box = this.$route.params.query
        this.page_num = parseInt(this.$route.params.page)
        this.search()
    },
    methods:{
        search()
        {
            this.axios.get('/ding/search',{params:
                {
                    keyword:this.search_box,
                    type: this.search_index,
                    page: this.page_num
                }
            })
            .then(res => {
                if(res.data.data.length == 0)
                {
                    this.is_result = true
                    this.search_items = []
                }
                else{
                    this.search_items = res.data.data
                    this.is_result = false
                }
                this.res_total = res.data.res_total
            })
        },
        change_index(num)
        {
            this.search_index = num
        },
        gotosearch()
        {
            this.$router.push({path:"/ding/"+this.page_num+"/"+this.search_index+"/"+this.search_box})
            this.search()
        },
    }
}
</script>
<style>
.search-box{
    
    text-align: left !important;
}
.search-menu{
    padding-left: 100px !important;
}
.el-menu.el-menu--horizontal{
    border-bottom:#fff;
}
#result_list{
    width: 540px;
    padding-left: 111px;
    padding-top: 10px
}
.container{
    margin-bottom: 14px;
    border-collapse: collapse;
    width: 538px;
    font-size: 13px;
    line-height: 1.54;
    word-wrap: break-word;
    word-break: break-word;
    padding-bottom: 10px;
}
.title{
    padding-bottom: 0%;
    display: block;
    font-size: 1.37em;
    margin-block-start: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}
.abstract{
   font-size: 13px;
}
.showurl{
    color: green;
    font-size: 13px;
}
.match{
    color: #c00;
}
.page{
    padding-left: 111px;
    padding-bottom: 10ch;
}
</style>





