<template>
    <div class="index">
        <img src="../assets/logo.png" style="width:600px"/>
        <div style="margin-top: 15px;">
            <el-input placeholder="请输入搜索内容" v-model="input" class="input-box" style="width:60%">
                <el-button slot="append" icon="el-icon-search" @click="gotosearch"></el-button>
                
            </el-input>
            <!-- <el-button @click="speechInput">语音</el-button> -->
            <el-menu :default-active="activeIndex" class="menu" mode="horizontal" style="padding-left: 20%; width:60%">
                <el-menu-item index="-1"  @click="change_index('-1')">综合</el-menu-item>
                <el-menu-item index="1"  @click="change_index('1')">文学院</el-menu-item>
                <el-menu-item index="2"  @click="change_index('2')">软件之家</el-menu-item>
                <el-menu-item index="3"  @click="change_index('3')">数学院</el-menu-item>
                <el-menu-item index="4"  @click="change_index('4')">法学院</el-menu-item>
                <el-menu-item index="5"  @click="change_index('5')">物理学院</el-menu-item>
            </el-menu>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            activeIndex: "-1",
            input: ""
        }
    },
    methods: {
        gotosearch()
        {
            if(this.input == "")
            {
                alert("请输入搜索内容")
            }
            else{
                console.log(this.activeIndex)
                this.$router.push({path:"/ding/1/"+this.activeIndex+"/"+this.input})
            }
        },
        change_index(num)
        {
            this.activeIndex = num
        }
        
    }
}
</script>
<style>
.input-box{
background-color: #fff;
}
.menu{
align-content: center;
text-align: center;
padding-left: 20% !important;
}
.el-menu.el-menu--horizontal{
    border-bottom:#fff !important;
}
.index{
  text-align: center
}
</style>




